import { useEffect, useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import backgroundMusic from './audio/bg.mp3'; // ng dn ti file nhc nn
import Home from './page/Home';
import Intro from './page/Intro';
import Success from './page/Success';

function App() {
  const [audio] = useState(new Audio(backgroundMusic)); // To i tng m thanh
  const [isMusicPlaying, setIsMusicPlaying] = useState(false); // Trng thi  kim tra nhc

  const startMusic = () => {
    if (!isMusicPlaying) {
      audio.play().then(() => {
        setIsMusicPlaying(true);
        console.log("Nhc nn bt u pht!");
      }).catch((error) => {
        console.error("Khng th pht nhc t ng:", error);
      });

      // Lp li nhc nn
      audio.loop = true;
    }
  };

  useEffect(() => {
    return () => {
      audio.pause(); // Dng pht nhc khi component b unmount
    };
  }, [audio]);

  return (
    <Routes basename={process.env.PUBLIC_URL}>
      <Route path="/for-u" element={<Intro startMusic={startMusic} />} />
      <Route path="/home" element={<Home />} />
      <Route path="/success" element={<Success />} />
    </Routes>
  );
}

export default function AppWrapper() {
  return (
    <BrowserRouter>
      <App />
    </BrowserRouter>
  );
}
