import React, { useState } from 'react';
import Heart from 'react-heart';
import { useNavigate } from 'react-router';
import '../css/intro.css';

function Intro({ startMusic }) {
  const [active, setActive] = useState(false);
  const navigate = useNavigate();

  // Hm x l khi nhn nt Play
  const handlePlayClick = () => {
    startMusic();  // Pht nhc
    navigate('/home');  // Sau  chuyn hng n trang Home
  };

  return (
    <div className="bg">
      <div className="title">
      </div>
      <div className="intro">
        <div className="intro-text">
          <p>
            Hi! cng chi mt tr chi nh nhaa!!!
            <br />
            <div style={{ textAlign: 'center' }}>   
              <span style={{ color: 'red' }}>
                I wish you happy with the game{' '}
                <span style={{ display: 'inline-block', verticalAlign: 'middle', width: '1rem' }}>
                  <Heart isActive={active} onClick={() => setActive(!active)} />
                </span>
                .
              </span>
            </div>
          </p>
          {/* Gi hm handlePlayClick khi nhn nt Play */}
          <button onClick={handlePlayClick}>Play</button>
        </div>
      </div>
    </div>
  );
}

export default Intro;
