import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router';
import applauseSound from '../audio/applause.mp3';
import clickSound from '../audio/click.mp3'; // m thanh nhn nt
import explosionSound from '../audio/explosion.mp3'; // m thanh n tung
import '../css/airplane.css';
import airplanePilotImage from '../img/airplane_pilot.png'; // nh phi cng ngi trn my bay

function Airplane() {
  const [energy, setEnergy] = useState(0);
  const [airplanePosition, setAirplanePosition] = useState(0);
  const [airplaneFlyUp, setAirplaneFlyUp] = useState(false);
  const [note, setNote] = useState('');
  const [showHelpMessage, setShowHelpMessage] = useState(false);
  const [showExplosion, setShowExplosion] = useState(false);
  const [showCongratulatoryMessage, setShowCongratulatoryMessage] = useState(false);
  const navigate = useNavigate();
  const phrases = [
    " C ln! Bn ang gip ti rt nhiu!",
    " My bay chun b ct cnh!",
    " Cm n bn, phi cng rt vui!",
    " Bn l ngi hng ca ti!",
    " Cht na thi, my bay sp bay ln tri ri!",
    "Mt cht na thi, c gi xinh p!",
    "im n ca chng ta l s hnh phc"
  ];

  // Hin th dng ch "Gip ti vi" sau 5 giy nu ngi chi khng tng tc
  useEffect(() => {
    const timer = setTimeout(() => {
      setShowHelpMessage(true);
    }, 5000);
    return () => clearTimeout(timer);
  }, []);

  const playSound = (sound) => {
    const audio = new Audio(sound);
    audio.play();
  };

  const handleTakeoff = () => {
    // Kim tra nng lng  quyt nh cch bay
    if (energy > 99) {
      setAirplaneFlyUp(true);
      playSound(applauseSound); // Pht m thanh v tay
      setShowCongratulatoryMessage(true); // Hin th thng ip chc mng

      setTimeout(() => {
        alert('Ct cnh thnh cng!');
        navigate('/success'); // Chuyn hng sang trang thnh cng
      }, 2000);
    } else {
      // Khi nng lng di 60%, pht m thanh n v hin th thng ip
      setShowExplosion(true);
      playSound(explosionSound); // Pht m thanh n tung
      setNote(' Sng l khng vi n i =)))'); // Hin th thng ip
      setTimeout(() => {
        setShowExplosion(false);
      }, 2000);
    }
  };

  const increaseEnergy = () => {
    setEnergy(prevEnergy => Math.min(prevEnergy + 5, 100)); // Tng 10% nng lng mi ln nhn nt
    setAirplanePosition(prev => Math.min(prev + 5, 100)); // Di chuyn my bay theo phn trm nng lng

    // Chn mt cu ngu nhin t danh sch
    const randomPhrase = phrases[Math.floor(Math.random() * phrases.length)];
    setNote(randomPhrase);
    playSound(clickSound); // Pht m thanh nhn nt

    // Nu nng lng t 100%, my bay s bay ln
    if (energy + 10 >= 100) {
      setAirplaneFlyUp(true); // Khi nng lng t 100%, my bay s bay ln
      setTimeout(() => {
        alert('My bay  t 100%! Ct cnh!');
        playSound(applauseSound, 0.7); // Pht m thanh v tay
        setShowCongratulatoryMessage(true); // Hin th thng ip chc mng
        navigate('/success'); // Chuyn hng sang trang thnh cng
      }, 2000);
    }
  };

  const handleNoHelpClick = () => {
    // Ly kch thc ca ca s
    const windowWidth = window.innerWidth;
    const windowHeight = window.innerHeight;

    // To v tr ngu nhin cho nt
    const randomX = Math.random() * (windowWidth - 100);
    const randomY = Math.random() * (windowHeight - 50);

    // Cp nht v tr cho nt khng gip
    const noHelpButton = document.querySelector('.no-help-button');
    noHelpButton.style.position = 'absolute';
    noHelpButton.style.left = `${randomX}px`;
    noHelpButton.style.top = `${randomY}px`;

    playSound(clickSound); // Pht m thanh nhn nt khi khng gip
  };

  return (
    <div className="airplane-container">
      <h1>Bn hy gip phi cng ny chinh phc my bay v bu tri nhaaaa !!!</h1>
      <div className="pilot-gif">
        <a src="../img/airplane.jpg" alt="Cute Pilot" />
      </div>
      <div className="buttons-container">
        <button className="help-button" onClick={increaseEnergy}>Gip</button>
        <button className="no-help-button" onClick={handleNoHelpClick}>
          Khng gip
        </button>
      </div>
      <button className="takeoff-button" onClick={handleTakeoff}>
        Ct cnh
      </button>

      <div className="energy-container">
        <div className="energy-bar" style={{ width: `${energy}%` }} />

        <div
          className={`airplane ${airplaneFlyUp ? 'fly-up' : ''}`} 
          style={{
            left: `${airplanePosition}%`,
            transition: 'left 1s ease',
          }}
        >
          <img src={airplanePilotImage} alt="Airplane with Pilot" className="airplane-icon" />
        </div>
      </div>

      {note && <div className="cute-note">{note}</div>}
      
      {showHelpMessage && !note && (
        <div className="help-message">
           Gip ti vi, phi cng cn bn!
        </div>
      )}

      {showExplosion && (
        <div className="explosion">
           BOOM! 
        </div>
      )}

      {showCongratulatoryMessage && (
        <div className="congratulations">
           Ct cnh thnh cng!  
        </div>
      )}
    </div>
  );
}

export default Airplane;
